@import url(https://fonts.googleapis.com/css?family=Lexend+Deca&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
  box-sizing:border-box;
  font-size: 100%;
  margin: 0;
  padding: 0;
}




.App {
  text-align: center;
  position:absolute;
  width:100%;
  min-height:100%;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  background:white;
}

.Main{
  flex:1 1;
  width:100%;
  height:100%;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  background:white;
}

.center-secs{
  flex:1 1;
  width:100%;
  height:100%;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;

}



.sec-title{
   animation: text-shadow 2.5s linear infinite;
    font-weight: 900;
    line-height: 1;
     transition-delay: 2s;
}



.montiel{
border-radius:100%;
margin:0px 5px;
   animation: text-shadow-an-flex 2.5s linear infinite;
     transition-delay: 2s;

}



@keyframes text-shadow {
    0% {  
        transform: translateY(0);
        
    }

    20% {  
        transform: translateY(-1em);
        
    }

    40% {  
        transform: translateY(0.5em);
       
    }
    
   60% {
        transform: translateY(-0.25em);
        
    }

    80% {  
        transform: translateY(0);
        
    }
}



@keyframes text-shadow-an-flex {
    0% {  
transform:scale(1.2);
        
    }

    20% {  
transform:scale(.6);
        
    }

    40% {  
transform:scale(1.4);
       
    }
    
   60% {
transform:scale(1);
        
    }

    80% {  
transform:scale(1.2);
        
    }
}




.snow {
  position: fixed;
  left: 50%;
  top: 50%;
  width: 0.4vw;
  height: 0.4vw;
  margin: -0.2vw 0 0 9.8vw;
  animation: ease 200s alternate infinite;
  pointer-events: none;
}
.snow::before, .snow::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transform-origin: -10vw;
}
.snow::before {
  background: white;
  opacity: 0.4;
  animation: drift ease alternate infinite;
}
.snow::after {
  background: white;
  opacity: 0;
  box-shadow: 0 0 0vw 0vw azure;
  animation: drift ease alternate infinite, flash ease infinite;
}

